import React, { useState } from "react";
const Button = (props: any) => {
  const [count, setCount] = useState();

  const show = () => {
    setCount(count + 1);
    props.show(count);
  };

  return (
    <>
      <button onClick={show}>{props.children ? props.children : "默认文案"}</button>
    </>
  );
};

export default Button;
